* {
  margin: 0;
}

.logo_image {
  position: relative;
  width: 100%;
  text-align: center;
  padding-top: 10px;
}

.logo_image img {
  position: relative;
  width: 50%;
}

h1 {
  font-size: 20px;
  text-align: center;
  margin-top: 5px;
  margin-bottom: 10px;
}

.page {
  width: 100%;
  height: 100%;
}

.button_pay {
  width: 100%;
  display: flex;
  position: relative;
  margin-top: 10px;
}

.button_pay button {
  border-radius: 5px;
  border: 0px;
  position: relative;
  text-align: center;
  margin: auto;
  width: 75%;
  font-size: 20px;
  color: #fff;
  background: #5b9bd5;
  height: 40px;
}

.button_pay:active {
  opacity: 0.6;
}

.text_description {
  padding: 0 10px 0 10px;
  text-align: center;
  margin-top: 10px;
}

.text_description div {
  color: #2b81d1;
  text-decoration: underline;
}

.text_description div:active {
  opacity: 0.6;
}

.PanelHeader-content {
  font-weight: 600 !important;
}